import { Component, OnInit } from '@angular/core';
import {Degree, Department} from '../entity';
import { EntityService } from '../entity.service';

@Component({
  selector: 'app-degree',
  templateUrl: './degree.component.html',
  styleUrls: ['./degree.component.css']
})
export class DegreeComponent implements OnInit {
  private n: number;
  private dataShow: Degree[]; // 
  public leng: number;                 // 
  public currentPag: number;           // 
  public pagNums: number;              // 
  public oldCurrentPag: number;
  public oldPagNums: number;
  degrees: Degree[];
  constructor(private degreeService: EntityService) { }
  /*/*/
  ngOnInit() {
    this.getDegrees();
  }
  // DoCheck DoCheck
  ngDoCheck(): void {
    if (this.currentPag !== this.oldCurrentPag || this.pagNums !== this.oldPagNums) {
      this.dataShow = [];
      const curren = this.currentPag - 1;
      const pagNum = this.pagNums;
      let startNum = curren * pagNum;
      for ( let i = 0; i < pagNum; i++, startNum++) {
        if ( this.degrees[startNum] !== null) {
          this.dataShow.push(this.degrees[startNum]);
        }
      }
      this.oldCurrentPag = this.currentPag;
      this.oldPagNums = this.pagNums;
    }
  }
// 
  currentPagHandel(pag: number) {
    this.currentPag = pag;
  }
  // 
  pagNumsHandel(pagNum: number) {
    this.pagNums = pagNum;
  }
  getDegrees(): void {
    this.degreeService.getDegrees ()
      .subscribe(res => this.degrees = res);
  }
  delete(degree: Degree | number): void {
    this.degrees = this.degrees.filter(h => h !== degree);
    this.degreeService.deleteDegree(degree).subscribe();
  }
}
