import { Component, OnInit } from '@angular/core';
import { Department, Teacher } from '../entity';
import { EntityService } from '../entity.service';
@Component({
  selector: 'app-teacher',
  templateUrl: './teacher.component.html',
  styleUrls: ['./teacher.component.css']
})
export class TeacherComponent implements OnInit {
  private n: number;
  private dataShow: Teacher[]; // 
  public leng: number;                 // 
  public currentPag: number;           // 
  public pagNums: number;              // 
  public oldCurrentPag: number;
  public oldPagNums: number;
  teachers: Teacher[];
  constructor(private teacherService: EntityService) { }
  /*/*/
  ngOnInit() {
    this.getTeachers();
  }
  // DoCheck DoCheck
  ngDoCheck(): void {
    if (this.currentPag !== this.oldCurrentPag || this.pagNums !== this.oldPagNums) {
      this.dataShow = [];
      const curren = this.currentPag - 1;
      const pagNum = this.pagNums;
      let startNum = curren * pagNum;
      for ( let i = 0; i < pagNum; i++, startNum++) {
        if ( this.teachers[startNum] !== null) {
          this.dataShow.push(this.teachers[startNum]);
        }
      }
      this.oldCurrentPag = this.currentPag;
      this.oldPagNums = this.pagNums;
    }
  }
// 
  currentPagHandel(pag: number) {
    this.currentPag = pag;
  }
  // 
  pagNumsHandel(pagNum: number) {
    this.pagNums = pagNum;
  }
  getTeachers(): void {
    this.teacherService.getTeachers ()
      .subscribe(res => this.teachers = res);
  }
  delete(teacher: Teacher | number): void {
    this.teachers = this.teachers.filter(h => h !== teacher);
    this.teacherService.deleteTeacher(teacher).subscribe(res => alert(res.message));
  }
}
